
.Product_Scroller{
    width: 90%;
    margin: auto;
    border-radius: 15px;
    height: 17rem;
    background-color: #353E55;
    border: .5rem solid rgba(73, 10, 155, 0.0000000);
    overflow-x: scroll ;
    max-width: 1400px;
  z-index: 1;
  direction: rtl;
}
.Product_Scroller::-webkit-scrollbar{
    display: none;
}
  .Product_Scroller .Product_Container{
    width: max-content;
    height: 16rem;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: right;
    gap: .5rem;
    padding: 1rem;

}
  .Product_Scroller .Product_Container .Product_Simbol{
    width: 10rem;
    height: 16rem;
    padding: .5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #eee;
    font-family: iran-sans;
    font-size: .8em;
}
  .Product_Scroller .Product_Container .Product_Simbol span{

    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;

}
  .Product_Scroller .Product_Container .Product_Simbol img{
    width: 50%;
}
  .Product_Scroller .Product_Container .Product{
    width: 12rem;
    height: 16rem;
    padding: 1.5rem;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: inset 10px 5px 50px 0 rgb(220, 220, 220);
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
  .Product_Scroller .Product_Container .Product img{
    height: 80%;
    padding: .5rem;


}
  .Product_Scroller .Product_Container .Product h6{
    width: 90%;
    position: absolute;
    bottom: 10px;
    text-align: right;
}

.ProductBox{
  display: flex;
  width: 90%;
  align-items: center;
  border: 2px solid #353E55;
  border-radius: 20px;
  overflow: hidden;
  margin: 1rem auto;
  max-width: 1400px;
  gap: .1rem;

}
.product_Container{
  width: 100%;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 1rem;
  gap: .5rem;

}

.product_Container p{
  width: 100%;
  font-family: iran-sans;

}
.product_Container .showmore{
  font-size: .8em;
  color: #353E55;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2px;
  width: 6rem;
  height: 2rem;
  border: 1px solid #353E55;
  border-radius: 5px;
  cursor: pointer;
}
.product_Container .showmore:hover{
  font-size: .8em;
  color: #d6ae2b;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2px;
  width: 6rem;
  border: 1px solid #d6ae2b;
  border-radius: 5px;

}
.product_Container .showmore:hover svg{
  color: #d6ae2b;
  fill: #d6ae2b;

}
.product_Container .showmore svg{
  font-size: .8em;
  color: #353E55;
  fill: #353E55;
}
.product_Container .title{
  text-align: right;
  color: #034141;

}
.product_Container .title2{
  text-align: right;
  font-size: .6em;
  color: #909090;

}

.product_Container .product{
  max-width: 50%;
  min-width: 40%;
  width: 25%;
  height: 40%;
  transition: .5s;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  position: relative;
  padding: 2vw;
  z-index: 2;
  margin: .2rem;
}
.product_Container .product:hover{
  scale: 1.1;
}
.product_Container .product img{
  width: 100%;
  height: 100%;
  object-fit: contain;
  z-index: 2;

}






