@import url('https://fonts.googleapis.com/css?family=Open+Sans');
.Banner{
    background-color: #f9f8f7;
    width: 100vw;
    font-size: calc(10px + 2vmin);
    color: #090807;
    overflow: hidden;
    padding-top: 10px;
    padding-bottom: 10px;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 1500px;

}
.slick-slider{
    background-color: #f9f8f7;
    width: 100vw;
    font-size: calc(10px + 2vmin);
    color: #090807;
    overflow: hidden;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 1500px;


}
.Banner img{
    background-color: #f9f8f7;
    width: 100%;
    font-size: calc(10px + 2vmin);
    color: #090807;
    overflow: hidden;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
}




@media only screen and (max-width: 900px) {
    .Banner{
        background-color: #f9f8f7;
        width: 100vw;
        font-size: calc(10px + 2vmin);
        color: #090807;
        overflow: hidden;
        border-radius: 10px;
        padding: 5px;


    }
    .slick-slider{
        background-color: #f9f8f7;
