        color: #090807;
        overflow: hidden;
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        max-width: 1500px;
    
    
    }
    .Banner img{
        background-color: #f9f8f7;
        width: 100%;
        font-size: calc(10px + 2vmin);
        color: #090807;
        overflow: hidden;
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
    }
    
    
    

