.directory-item-container {
  width: 12%;
  height: 300px;
  
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  justify-content: center;
  margin: 0 7.5px 15px;
  direction: rtl;

  &:hover {
    scale: 1.01;
    .background-image {
      opacity: 0.8;
    }
    .directory-item-body {
      opacity: 0.9;
    }
  }
  .background-image {
    width: 100%;
    height: 80%;
    border-radius: 20px;
    background-color:#f2f2f2;
    
    img {

      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  .directory-item-body {
    height: 20%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5px;

    h3 {
      font-weight: bold;
      margin: 0 3px 0;
      font-size: 1em;
      color: #4a4a4a;
      text-align: right;
    width: 100%;

    }

    p {
      font-weight: lighter;
      font-size: 0.7em;
    background-color: #353E55;
      color: #FFF;
      padding: 3px 8px;
      border-radius: 10px;
    }
  }

  @media (max-width: 768px) {
    height: 260px;
    width: 28%;

    .directory-item-body {
      height: 20%;
      h3 {
        font-size: 0.8em;
      }
      p {
        font-size: 0.5em;
      }
    }
  }

  @media (max-width: 576px) {
    height: 260px;
    width: 40%;

    .directory-item-body {
      height: 20%;
      h3 {
        font-size: 0.7em;
        margin: 0 1px 0;
      }
      p {
        font-size: 0.5em;
      }
    }
  }
}
