.Videos{
    width: 90%;
    z-index: 1;
    max-width: 1400px;
  margin: auto;
}
h3{
  direction: rtl;
  padding-right: 10px;
  font-size: .8em;
}
.recipe-container {

    padding: 30px 0;
    width: 100%;

  }
  
  
  .swiper {
    height: 100%;
    overflow-x: scroll;

  }
  .swiper::-webkit-scrollbar {
    height: 5px;

  }
  .swiper::-webkit-scrollbar-thumb {
    width: 5px;
    background-color: #353E55;
    border-radius: 5px;

  }
  .swiper-wrapper{
    width: max-content;
    display: flex;
  }
  .swiper-scrollbar {
    --swiper-scrollbar-bottom: 0;
    --swiper-scrollbar-drag-bg-color: #F9F8F7;
    --swiper-scrollbar-size: 5px;
  }
  
  .post {
    max-width: 500px;
    font-size: 1rem;
    font-weight: 500;
    background: #f9f8f710;
    border-radius: 10px;
    padding: 16px;
    margin-bottom: 16px;
    margin: 1rem;

  }
  
  .post-video {
    width: 100%;
    object-fit: cover;
    overflow: hidden;
    border-radius: 6px;
  }
  
  
  @media only screen and (max-width: 900px) {
    .Videos{
        width: 100vw;
        z-index: 1;
    
    }
    .recipe-container {

        padding: 5px 0;
        width: 100%;
    
      }
      
      .swiper {
        width: 100%;
        height: 100%;
        overflow-x: scroll;
    
      }
      
      .swiper-scrollbar {
        --swiper-scrollbar-bottom: 0;
        --swiper-scrollbar-drag-bg-color: #F9F8F7;
        --swiper-scrollbar-size: 5px;
      }
      
      .post {
        max-width: 300px;
        font-size: 1rem;
        font-weight: 500;
        background: #f9f8f710;
        border-radius: 10px;
        padding: 16px;
        margin-bottom: 16px;
        margin: 1rem;
      }
      
      .post-img {
        width: 100%;
        max-width: 300px;
        object-fit: cover;
        overflow: hidden;
        aspect-ratio: 4/3;
        border-radius: 6px;
        user-select: none;
        pointer-events: none;
      }
      
  }