.ads{
    width: 100vw;
    color: #777;
    font-size: 20px;
    text-wrap: wrap;
    display: grid;
    align-items: center;
    justify-content: center;
    text-align: center;
    line-height: 1.5rem;
    z-index: 1;
    position: relative;
    padding: 2rem;

  overflow: hidden;
  }

  .holderCircle { 
    width: 500px;
     height: 500px;
      border-radius: 100%;
       margin: 0; 
      position: relative; 
      display: flex;
      align-items: center;
      justify-content: center;
    }


  .dotCircle {
     width: 100%;
      height: 100%;
       position: absolute;
        margin: auto; 
           border-radius: 100%;
            z-index: 20; 
        }
  
  
  .dotCircle  .itemDot {
     display: flex;
     align-items: center;
     justify-content: center; 
     width: 80px; 
     height: 80px;
      position: absolute;
         border-radius: 20px;
          text-align: center; 
          line-height: 80px; 
          font-size: 10px;
          padding: 5px;
           z-index: 3;
            cursor: pointer;

             }
  
  
     .dotCircle  .itemDot .forActive {
     width: 56px;
      height: 56px;
       position: absolute;
            display: none;
         }
  
  .dotCircle  .itemDot .forActive::after {
     content: '';
      width: 5px;
       height: 5px;
        border: 3px solid #353E55;
         bottom: -31px;
          left: -14px;
           filter: blur(1px);
            position: absolute;
             border-radius: 100%; 
            }
 
  .dotCircle  .itemDot .forActive::before { 
    content: '';
     width: 6px;
      height: 6px; 
      filter: blur(5px); 
      top: -15px; 
      position: absolute;
       transform: rotate(-45deg);
        border: 6px solid #353E55;
         right: -39px;
         }
 
  .dotCircle  .itemDot.active .forActive {
     display: block; 
}
  
  .round { 
    position: absolute;
      width: 410px;
       height: 410px; 
       border: 2px dotted #353E55;
        border-radius: 100%;
         -webkit-animation: rotation 100s infinite linear;
         }
  
  .dotCircle .itemDot:hover, 
  .dotCircle .itemDot.active {
    color: #FFF;
     transition: 0.5s; 
     background: #353E55;
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#4a8bc767',endColorstr='#5733bb7c', GradientType=1);
          -webkit-box-shadow: 0 30px 30px 0 rgba(0, 0, 0, .13);
           -moz-box-shadow: 0 30px 30px 0 rgba(0, 0, 0, .13);
           box-shadow: 0 30px 30px 0 rgba(0, 0, 0, .13); 
        }

  
  .dotCircle .itemDot { 
    font-size: 40px;
    background: #e6d8ff1c;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);

 }


  .contentCircle { 
    width: 250px; 
    border-radius: 50%; 
    color: #222222; 
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

  .contentCircle .CirItem {
     border-radius: 100%;
     display: flex;
     align-items: center;
     justify-content: center;
      color: #F9F8F7;
       position: absolute;
        text-align: center;
           opacity: 0; 
           transform: scale(0);
            transition: 0.5s;
             font-size: 15px;
              width: 100%;
               height: 100%;
                top: 0;
                 right: 0;
                  margin: auto;

                 }

  .CirItem.active { 
    z-index: 1; 
    opacity: 1;
     transform: scale(1);
      transition: 0.5s; 
      display: flex;
      align-items: center;
      justify-content: center;
    }
   .dotCircle i { 
    font-size: 35px;
         opacity: 0.8; 
        }
  
        .title-box .title {
            font-weight: 600;
             letter-spacing: 2px;
              position: relative;
               z-index: -1; 
           }
             .title-box span { 
                 font-weight: 800;
                  color: #353E55;
                 }

                 .contentCircle .CirItem i{
                  font-size: 150px;
                  opacity: 0.2; 
                  color: #999;
                  position: absolute;
 
                 }
